@import "../../../variables";

#plugin-gnosis-container {
  height: 100%;
  @include flex-full-grow-container;

  iframe {
    @include flex-full-grow-container;
    overflow: auto;
    box-sizing: border-box;
    border: 0;
  }

  .iframe-placeholder {
    @include flex-full-grow-container;
  }

  #dapps-container {
    padding: 0;
    display: flex;
    flex-flow: row wrap;
    margin: 0 -1rem 0 -1rem;

    li {
      margin: .5rem 1rem;
      flex: 1 0 7.5rem;
      list-style: none;
      background: $c-ebony-clay;
      padding: 1rem;
      cursor: pointer;
      transition: $basic-transition;

      &:hover {
        background: lighten($c-ebony-clay, 5%);
      }

      &.selected { 
        background: lighten($c-ebony-clay, 50%);
      }

      &.add-plugin {
        align-self: center;
        justify-content: center;
        text-align: center;
        background: 0;
        cursor: default;
      }

      div.logo-container {
        margin: .75rem auto .5rem auto;
        width: 3rem;
        height: 3rem;
        background-size: contain;
        background-repeat: no-repeat;
      }

      div.dapp-name {
        text-align: center;
        font-size: 2em;
        padding-bottom: 1rem;
      }

      div.dapp-desc {
        min-width: 0;
        overflow: hidden;
        text-align: center;
        // text-overflow-ellipsis: "...";
        text-overflow: ellipsis "...";
        font-size: 0.9em;
        line-height: 1.4em;
        color: $text-container-color;
      }
    }

    &.small-thumbs {
      margin: 0 -0.1875rem .375rem -0.1875rem;

      li {
        margin: .1875rem .1875rem;
        padding: .375rem;
        position: relative;

        .remove-btn {
          position: absolute;
          top: 0;
          right: 0;
        }

        div.logo-container {
          margin: .25rem auto .25rem auto;
          width: 1.5rem;
          height: 1.5rem;
          border-radius: .25rem;
        }
  
        div.dapp-name {
          text-align: center;
          font-size: 0.8em;
          padding-bottom: .375rem;
        }
  
        div.dapp-desc {
          min-width: 0;
          display: none;
        }
      }

    }
  }
}
