@import 'variables';

$titleBar-height: 5.625rem;

.wrapper {
	position: relative;
	z-index: 99;
	display: flex;
	align-items: center;
	width: 100%;
  margin-bottom: 1.875rem;

	.row {
		@include flex-center-center;
		gap: .625rem;

    &.dismiss {
      cursor: pointer;
    }
	}

	.backIcon {
		background-color: theme(background-icon);
		min-width: 2.5rem;
		min-height: 2.5rem;
		display: flex;
		align-items: center;
		justify-content: center;
		border-radius: $border-radius;
	}

	.backLabel {
		margin: 0;
		font-size: 1rem;
		font-weight: 300;
		transition: 0.3s ease-in-out opacity;
	}

	.infoIcon {
		width: 1.25rem;
		height: 1.25rem;
	}

	&:hover {
		.backLabel {
			opacity: 0.8;
		}
	}
}
